import { __assign } from "tslib";
/**
 * @description line shapes
 */

import * as _ from '@antv/util';
import { registerShape, registerShapeFactory } from './base';
import { getLinePath, getSplinePath } from '../util/path';
import { splitPoints, setStrokeStyle } from '../util/shape';
import { LineSymbols } from '../util/symbol';
var DOT_LINEDASH = [1, 1];
var DASH_LINEDASH = [5.5, 1]; // 

function _getAttrs(cfg) {
  var lineAttrs = cfg.style;
  setStrokeStyle(lineAttrs, cfg);

  if (cfg.size) {
    lineAttrs.lineWidth = cfg.size;
  }

  return lineAttrs;
} //  Marker 


function _getMarkerAttrs(cfg) {
  var markerAttrs = {
    lineWidth: 2,
    radius: 6
  };
  setStrokeStyle(markerAttrs, cfg);
  return markerAttrs;
} // 


function _drawPointShape(cfg, container) {
  var point = cfg.points[0];
  return container.addShape('circle', {
    attrs: {
      x: point.x,
      y: point.y,
      r: 2,
      fill: cfg.color
    }
  });
} //  path


function _getRangePath(points, smooth, isInCircle, cfg) {
  var isStack = cfg.isStack;
  var topPoints = [];
  var bottomPoints = [];

  for (var i = 0, len = points.length; i < len; i++) {
    var point = points[i];
    var tmp = splitPoints(point);
    bottomPoints.push(tmp[0]);
    topPoints.push(tmp[1]);
  }

  var topPath = _getSinglePath(topPoints, smooth, isInCircle, cfg);

  var bottomPath = _getSinglePath(bottomPoints, smooth, isInCircle, cfg);

  if (isStack) {
    return topPath;
  }

  return topPath.concat(bottomPath);
} //  path


function _getSinglePath(points, smooth, isInCircle, cfg) {
  var path;

  if (!smooth) {
    path = getLinePath(points, false);
    isInCircle && path.push(['Z']);
  } else {
    // 
    var constraint = cfg.constraint;
    isInCircle && points.length && points.push({
      x: points[0].x,
      y: points[0].y
    });
    path = getSplinePath(points, false, constraint);
  }

  return path;
} // get line path


function _getPath(cfg, smooth) {
  var points = cfg.points,
      isInCircle = cfg.isInCircle;
  var first = points[0];
  return _.isArray(first.y) ? _getRangePath(points, smooth, isInCircle, cfg) : _getSinglePath(points, smooth, isInCircle, cfg);
}

function _interpPoints(points, fn) {
  var tmpPoints = [];

  _.each(points, function (point, index) {
    var nextPoint = points[index + 1];
    tmpPoints.push(point);

    if (nextPoint) {
      tmpPoints = tmpPoints.concat(fn(point, nextPoint));
    }
  });

  return tmpPoints;
} // pathnull


function _getInterPath(points) {
  var path = points.map(function (point, index) {
    return index === 0 ? ['M', point.x, point.y] : ['L', point.x, point.y];
  });
  return path;
} // 


function _getInterPointShapeCfg(cfg, fn) {
  var points = _interpPoints(cfg.points, fn);

  return _getInterPath(points);
}

var LineShapeFactory = registerShapeFactory('line', {
  defaultShapeType: 'line',
  drawShape: function drawShape(type, cfg, container) {
    var shape = this.getShape(type); //  ShapeFactotyBase.drawShape  shape 

    var shapeName = shape.name;
    var theme = this._theme;

    if (theme && theme.line && theme.line[shapeName]) {
      var defaultShapeStyle = theme.line[shapeName].default;
      cfg.style = _.mix({}, defaultShapeStyle, cfg.style);
    }

    var gShape = cfg.points.length === 1 && cfg.showSinglePoint ? _drawPointShape(cfg, container) : shape.draw(cfg, container);

    if (gShape) {
      gShape.setSilent('origin', cfg.origin);
      gShape.id = cfg.splitedIndex ? cfg.id + cfg.splitedIndex : cfg.id;
      gShape.name = this.name;
    }

    return gShape;
  },
  getMarkerStyle: function getMarkerStyle(type, markerCfg) {
    var shape = this.getShape(type);
    var markerStyle;

    if (shape.getMarkerStyle) {
      markerStyle = shape.getMarkerStyle(markerCfg);
    } else {
      markerStyle = _getMarkerAttrs(markerCfg);
      var symbol = LineSymbols[type];

      if (type === 'dot') {
        symbol = LineSymbols.line;
        markerStyle.lineDash = markerStyle.lineDash || DOT_LINEDASH;
      }

      if (type === 'dash') {
        symbol = LineSymbols.line;
        markerStyle.lineDash = markerStyle.lineDash || DASH_LINEDASH;
      }

      markerStyle.symbol = symbol;
    }

    var theme = this._theme;
    var shapeName = shape.name;

    if (theme && theme.line && theme.line[shapeName]) {
      markerStyle = _.mix({}, theme.line[shapeName].default, markerStyle);
    }

    return markerStyle;
  }
}); //  LineShapeFactory  shape

registerShape('line', 'line', {
  draw: function draw(cfg, container) {
    var attrs = _getAttrs(cfg);

    var path = _getPath(cfg, false);

    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  }
}); //  

registerShape('line', 'dot', {
  draw: function draw(cfg, container) {
    var attrs = _getAttrs(cfg);

    var path = _getPath(cfg, false);

    return container.addShape('path', {
      attrs: __assign(__assign({
        lineDash: DOT_LINEDASH
      }, attrs), {
        path: path
      })
    });
  }
}); //  - - -

registerShape('line', 'dash', {
  draw: function draw(cfg, container) {
    var attrs = _getAttrs(cfg);

    var path = _getPath(cfg, false);

    return container.addShape('path', {
      attrs: __assign(__assign({
        lineDash: DASH_LINEDASH
      }, attrs), {
        path: path
      })
    });
  }
}); // 

registerShape('line', 'smooth', {
  draw: function draw(cfg, container) {
    var attrs = _getAttrs(cfg);

    var coord = this.getCoord(); // 

    cfg.constraint = [[coord.start.x, coord.end.y], [coord.end.x, coord.start.y]];

    var path = _getPath(cfg, true);

    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  }
}); // step line,

registerShape('line', 'hv', {
  draw: function draw(cfg, container) {
    var attrs = _getAttrs(cfg);

    var path = _getInterPointShapeCfg(cfg, function (point, nextPoint) {
      return [{
        x: nextPoint.x,
        y: point.y
      }];
    });

    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  }
});
registerShape('line', 'vh', {
  draw: function draw(cfg, container) {
    var attrs = _getAttrs(cfg);

    var path = _getInterPointShapeCfg(cfg, function (point, nextPoint) {
      return [{
        x: point.x,
        y: nextPoint.y
      }];
    });

    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  }
});
registerShape('line', 'hvh', {
  draw: function draw(cfg, container) {
    var attrs = _getAttrs(cfg);

    var path = _getInterPointShapeCfg(cfg, function (point, nextPoint) {
      var middleX = (nextPoint.x + point.x) / 2;
      return [{
        x: middleX,
        y: point.y
      }, {
        x: middleX,
        y: nextPoint.y
      }];
    });

    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  }
});
registerShape('line', 'vhv', {
  draw: function draw(cfg, container) {
    var attrs = _getAttrs(cfg);

    var path = _getInterPointShapeCfg(cfg, function (point, nextPoint) {
      var middleY = (point.y + nextPoint.y) / 2;
      return [{
        x: point.x,
        y: middleY
      }, {
        x: nextPoint.x,
        y: middleY
      }];
    });

    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  }
});
export default LineShapeFactory;