import * as _ from '@antv/util';

var ElementParser =
/** @class */
function () {
  function ElementParser(cfg) {
    _.assign(this, cfg);

    this.init();
  }

  ElementParser.prototype.init = function () {
    this.config = {
      type: this.type,
      position: {
        fields: this.positionFields
      },
      widthRatio: {
        // 
        column: this.widthRatio || 3 / 5,
        rose: 0.9999999,
        multiplePie: 1 / 1.3
      }
    };
  };

  return ElementParser;
}();

export default ElementParser;