/**
 *  plot theme object theme
 */
var PLOT_THEME_MAP = {};
/**
 * 
 * @param type
 * @param theme
 */

export function registerTheme(type, theme) {
  PLOT_THEME_MAP[type.toLowerCase()] = theme;
}
/**
 * 
 * @param type
 */

export function getTheme(type) {
  return PLOT_THEME_MAP[type.toLowerCase()] || {};
}