function parsePoints(shape) {
  var parsedPoints = [];
  var coord = shape.get('coord');
  var points = shape.get('origin').points;

  _.each(points, function (p) {
    parsedPoints.push(coord.convertPoint(p));
  });

  return parsedPoints;
}

function getDefaultStyle() {
  return {
    areaStyle: {
      opacity: 0.2
    },
    lineStyle: {
      lineWidth: 2,
      opacity: 0.1
    }
  };
}

