import * as _ from '@antv/util';

function isSameShape(shape1, shape2) {
  if (shape1 && shape2 && shape1 === shape2) {
    return true;
  }

  return false;
}

function isPointInBBox(point, bbox) {
  if (point.x >= bbox.minX && point.x <= bbox.maxX && point.y >= bbox.minY && point.y <= bbox.maxY) {
    return true;
  }

  return false;
}

