import * as _ from '@antv/util';
/**
 *  nullundefined 
 * @param data 
 * @param connectNulls 
 * @param field 
 */

export function splitData(data, connectNulls, field) {
  if (!data.length) return [];
  var splitData = [];
  var tmp = [];
  var yValue;
  data.forEach(function (obj) {
    yValue = obj._origin ? obj._origin[field] : obj[field];

    if (connectNulls) {
      //  Null  value = null 
      if (!_.isNil(yValue)) {
        tmp.push(obj);
      }
    } else {
      if (_.isArray(yValue) && _.isNil(yValue[0]) || _.isNil(yValue)) {
        if (tmp.length) {
          splitData.push(tmp);
          tmp = [];
        }
      } else {
        tmp.push(obj);
      }
    }
  });
  tmp.length && splitData.push(tmp);
  return splitData;
}