import { __assign } from "tslib";
/**
 * @description Interval Element shapes
 */

import * as _ from '@antv/util';
import { Marker } from '@antv/g';
import { registerShapeFactory, registerShape } from './base';
import { setFillStyle, setStrokeStyle } from '../util/shape';
import { getPointAngle } from '../util/path';
import { IntervalSymbols } from '../util/symbol';

_.each(IntervalSymbols, function (func, symbol) {
  // Interval  symbol
  //  Marker 
  //  Element
  Marker.symbolsFactory.register(symbol, func);
}); // 


function _getFillAttrs(cfg) {
  var attrs = cfg.style;
  setFillStyle(attrs, cfg);
  return attrs;
} // 


function _getLineAttrs(cfg) {
  var attrs = cfg.style;
  setStrokeStyle(attrs, cfg);
  return attrs;
} // 


function _getRectPoints(cfg, isPyramid) {
  if (isPyramid === void 0) {
    isPyramid = false;
  }

  var x = cfg.x,
      y = cfg.y,
      y0 = cfg.y0,
      size = cfg.size; //  4 
  // 1. x, y 
  // 2. yx
  // 3. xy
  // 4. x, y 

  var yMin;
  var yMax;

  if (_.isArray(y)) {
    yMin = y[0];
    yMax = y[1];
  } else {
    yMin = y0;
    yMax = y;
  }

  var xMin;
  var xMax;

  if (_.isArray(x)) {
    xMin = x[0];
    xMax = x[1];
  } else {
    xMin = x - size / 2;
    xMax = x + size / 2;
  }

  var points = [{
    x: xMin,
    y: yMin
  }, {
    x: xMin,
    y: yMax
  }];

  if (isPyramid) {
    // 
    // 
    // 1
    // |   2
    // 0
    points.push({
      x: xMax,
      y: (yMax + yMin) / 2
    });
  } else {
    // 
    // 1 ---- 2
    // |      |
    // 0 ---- 3
    points.push({
      x: xMax,
      y: yMax
    }, {
      x: xMax,
      y: yMin
    });
  }

  return points;
} //  path


function _getRectPath(points) {
  var path = [];
  var firstPoint = points[0];
  path.push(['M', firstPoint.x, firstPoint.y]);

  for (var i = 1, len = points.length; i < len; i++) {
    path.push(['L', points[i].x, points[i].y]);
  }

  path.push(['L', firstPoint.x, firstPoint.y]); // 

  path.push(['z']);
  return path;
} //  Line 


function _getLinePoints(cfg) {
  var x = cfg.x,
      y = cfg.y,
      y0 = cfg.y0;

  if (_.isArray(y)) {
    return y.map(function (yItem, idx) {
      return {
        x: _.isArray(x) ? x[idx] : x,
        y: yItem
      };
    });
  }

  return [{
    x: x,
    y: y
  }, {
    x: x,
    y: y0
  }];
} //  tick shape  6 


function _getTickPoints(cfg) {
  var x = cfg.x,
      y = cfg.y,
      y0 = cfg.y0,
      size = cfg.size;
  var yMin;
  var yMax;

  if (_.isArray(y)) {
    yMin = y[0];
    yMax = y[1];
  } else {
    yMin = y0;
    yMax = y;
  }

  var xMax = x + size / 2;
  var xMin = x - size / 2; // tick 
  // 0 - 2 - 1
  //     |
  // 4 - 3 - 5

  return [{
    x: xMin,
    y: yMax
  }, {
    x: xMax,
    y: yMax
  }, {
    x: x,
    y: yMax
  }, {
    x: x,
    y: yMin
  }, {
    x: xMin,
    y: yMin
  }, {
    x: xMax,
    y: yMin
  }];
} //  tick  path


function _getTickPath(points) {
  return [['M', points[0].x, points[0].y], ['L', points[1].x, points[1].y], ['M', points[2].x, points[2].y], ['L', points[3].x, points[3].y], ['M', points[4].x, points[4].y], ['L', points[5].x, points[5].y]];
} //  path


function _getFunnelPath(cfg, isFunnel) {
  var path = [];
  var points = cfg.points,
      nextPoints = cfg.nextPoints;

  if (!_.isNil(nextPoints)) {
    path.push(['M', points[0].x, points[0].y], ['L', points[1].x, points[1].y], ['L', nextPoints[1].x, nextPoints[1].y], ['L', nextPoints[0].x, nextPoints[0].y], ['Z']);
  } else if (isFunnel) {
    path.push(['M', points[0].x, points[0].y], ['L', points[1].x, points[1].y], ['L', points[2].x, points[2].y], ['L', points[3].x, points[3].y], ['Z']);
  } else {
    path.push(['M', points[0].x, points[0].y], ['L', points[1].x, points[1].y], ['L', points[2].x, points[2].y], // [ 'L', points[2].x, points[2].y ],
    ['Z']);
  }

  return path;
}

function _getThetaAngle(point, coord) {
  var startAngle;
  var endAngle;
  var startPoint;
  var endPoint;

  if (!_.isArray(point.x) && _.isArray(point.y)) {
    // xyx
    point.x = [point.x, point.x];
  }

  if (_.isArray(point.x)) {
    startPoint = {
      x: point.x[0],
      y: point.y[0]
    };
    endPoint = {
      x: point.x[1],
      y: point.y[1]
    };
    startAngle = getPointAngle(coord, startPoint);
    endAngle = getPointAngle(coord, endPoint);

    if (endAngle <= startAngle) {
      // 
      endAngle = endAngle + Math.PI * 2;
    }
  } else {
    endPoint = point;
    startAngle = coord.startAngle;
    endAngle = getPointAngle(coord, endPoint);
  }

  return {
    startAngle: startAngle,
    endAngle: endAngle
  };
}

var IntervalShapeFactory = registerShapeFactory('interval', {
  defaultShapeType: 'rect',
  getDefaultPoints: function getDefaultPoints(pointInfo) {
    return _getRectPoints(pointInfo);
  }
}); //  rect

registerShape('interval', 'rect', {
  draw: function draw(cfg, container) {
    var attrs = _getFillAttrs(cfg);

    var path = _getRectPath(cfg.points);

    path = this.parsePath(path);
    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  },
  getMarkerStyle: function getMarkerStyle(markerCfg) {
    var isInCircle = markerCfg.isInCircle;
    var markerStyle = {
      symbol: isInCircle ? 'circle' : 'square',
      radius: isInCircle ? 4.5 : 4
    };
    setFillStyle(markerStyle, markerCfg);
    return markerStyle;
  }
}); // 

registerShape('interval', 'hollowInterval', {
  draw: function draw(cfg, container) {
    var attrs = _getLineAttrs(cfg);

    var path = _getRectPath(cfg.points);

    path = this.parsePath(path);
    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  },
  getMarkerStyle: function getMarkerStyle(markerCfg) {
    var isInCircle = markerCfg.isInCircle;
    var markerStyle = {
      symbol: isInCircle ? 'circle' : 'square',
      radius: isInCircle ? 4.5 : 4
    };
    setStrokeStyle(markerStyle, markerCfg);
    return markerStyle;
  }
}); // 

registerShape('interval', 'line', {
  getPoints: function getPoints(pointInfo) {
    return _getLinePoints(pointInfo);
  },
  draw: function draw(cfg, container) {
    var attrs = _getLineAttrs(cfg);

    attrs.lineWidth = cfg.size || 1;

    var path = _getRectPath(cfg.points);

    path = this.parsePath(path);
    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  },
  getMarkerStyle: function getMarkerStyle(markerCfg) {
    var markerStyle = {
      symbol: 'line',
      radius: 5
    };
    setStrokeStyle(markerStyle, markerCfg);
    return markerStyle;
  }
}); // I 

registerShape('interval', 'tick', {
  getPoints: function getPoints(pointInfo) {
    return _getTickPoints(pointInfo);
  },
  draw: function draw(cfg, container) {
    var attrs = _getLineAttrs(cfg);

    var path = _getTickPath(cfg.points);

    path = this.parsePath(path);
    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  },
  getMarkerStyle: function getMarkerStyle(markerCfg) {
    var markerStyle = {
      symbol: 'tick',
      radius: 5
    };
    setStrokeStyle(markerStyle, markerCfg);
    return markerStyle;
  }
}); // 

registerShape('interval', 'funnel', {
  getPoints: function getPoints(pointInfo) {
    pointInfo.size = pointInfo.size * 2; //  size 

    return _getRectPoints(pointInfo);
  },
  draw: function draw(cfg, container) {
    var attrs = _getFillAttrs(cfg);

    var path = _getFunnelPath(cfg, true);

    path = this.parsePath(path);
    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  },
  getMarkerStyle: function getMarkerStyle(markerCfg) {
    var markerStyle = {
      symbol: 'square',
      radius: 4
    };
    setFillStyle(markerStyle, markerCfg);
    return markerStyle;
  }
}); // 

registerShape('interval', 'pyramid', {
  getPoints: function getPoints(pointInfo) {
    pointInfo.size = pointInfo.size * 2; //  size 

    return _getRectPoints(pointInfo, true);
  },
  draw: function draw(cfg, container) {
    var attrs = _getFillAttrs(cfg);

    var path = _getFunnelPath(cfg, false);

    path = this.parsePath(path);
    return container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        path: path
      })
    });
  },
  getMarkerStyle: function getMarkerStyle(markerCfg) {
    var markerStyle = {
      symbol: 'square',
      radius: 4
    };
    setFillStyle(markerStyle, markerCfg);
    return markerStyle;
  }
}); // TODOtop-line  shape 
// 

registerShape('interval', 'top-line', {
  draw: function draw(cfg, container) {
    var attrs = _.mix({}, _getFillAttrs(cfg));

    var style = cfg.style,
        points = cfg.points;

    var rectPath = _getRectPath(points);

    rectPath = this.parsePath(rectPath);
    delete attrs.stroke; // 

    var rectShape = container.addShape('path', {
      attrs: __assign(__assign({}, attrs), {
        zIndex: 0,
        path: rectPath
      })
    }); //  top line

    var topLinePath = [['M', points[1].x, points[1].y], ['L', points[2].x, points[2].y]];
    container.addShape('path', {
      zIndex: 1,
      attrs: {
        stroke: style.stroke || 'white',
        lineWidth: style.lineWidth || 1,
        path: this.parsePath(topLinePath)
      }
    });
    return rectShape;
  },
  getMarkerStyle: function getMarkerStyle(markerCfg) {
    var isInCircle = markerCfg.isInCircle;
    var markerStyle = {
      symbol: isInCircle ? 'circle' : 'square',
      radius: isInCircle ? 4.5 : 4
    };
    setFillStyle(markerStyle, markerCfg);
    return markerStyle;
  }
});
export default IntervalShapeFactory;