import extended from '../util/extended';
import interval from '../util/interval';
/**
 *  ticks wilkinson extended 
 * @param cfg 
 * @returns  ticks
 */

export default function linear(cfg) {
  var min = cfg.min,
      max = cfg.max,
      tickCount = cfg.tickCount,
      nice = cfg.nice,
      tickInterval = cfg.tickInterval;

  if (tickInterval) {
    return interval(min, max, tickInterval).ticks;
  }

  return extended(min, max, tickCount, nice).ticks;
}