import * as _ from '@antv/util';
export function splitPoints(obj) {
  var points = [];
  var x = obj.x;
  var y = obj.y;
  y = _.isArray(y) ? y : [y];

  _.each(y, function (yItem, index) {
    var point = {
      x: _.isArray(x) ? x[index] : x,
      y: yItem
    };
    points.push(point);
  });

  return points;
} // rename addFillAttrs

export function setFillStyle(attrs, cfg) {
  var color = cfg.color,
      opacity = cfg.opacity;

  if (color) {
    attrs.fill = color;
  }

  if (_.isNumber(opacity)) {
    attrs.opacity = attrs.fillOpacity = opacity;
  }
} // rename addStrokeAttrs

export function setStrokeStyle(attrs, cfg) {
  var color = cfg.color,
      opacity = cfg.opacity;

  if (color) {
    attrs.stroke = color;
  }

  if (_.isNumber(opacity)) {
    attrs.opacity = attrs.strokeOpacity = opacity;
  }
}