import * as _ from '@antv/util';
import { setFillStyle, setStrokeStyle } from '../util/shape';
import { registerShapeFactory, registerShape } from './base';

function _sortValue(oldValue) {
  var value = oldValue;

  if (!_.isArray(oldValue)) {
    value = [oldValue];
  } // 


  var sorted = value.sort(function (a, b) {
    return a < b ? 1 : -1;
  });
  var length = sorted.length;

  if (length < 4) {
    var min = sorted[length - 1];

    for (var i = 0; i < 4 - length; i++) {
      sorted.push(min);
    }
  }

  return sorted;
}

function _getPoints(x, y, width) {
  var yValues = _sortValue(y);

  var points = [{
    x: x,
    y: yValues[0]
  }, {
    x: x,
    y: yValues[1]
  }, {
    x: x - width / 2,
    y: yValues[2]
  }, {
    x: x - width / 2,
    y: yValues[1]
  }, {
    x: x + width / 2,
    y: yValues[1]
  }, {
    x: x + width / 2,
    y: yValues[2]
  }, {
    x: x,
    y: yValues[2]
  }, {
    x: x,
    y: yValues[3]
  }]; // 

  return points;
}

function _getPath(points) {
  var path = [['M', points[0].x, points[0].y], ['L', points[1].x, points[1].y], ['M', points[2].x, points[2].y], ['L', points[3].x, points[3].y], ['L', points[4].x, points[4].y], ['L', points[5].x, points[5].y], ['Z'], ['M', points[6].x, points[6].y], ['L', points[7].x, points[7].y]];
  return path;
}

function _getAttrs(cfg) {
  var lineAttrs = cfg.style;
  setFillStyle(lineAttrs, cfg);
  setStrokeStyle(lineAttrs, cfg);
  return lineAttrs;
}

var KLineShapeFactory = registerShapeFactory('kline', {
  defaultShapeType: 'kline'
});
registerShape('kline', 'kline', {
  getPoints: function getPoints(pointInfo) {
    return _getPoints(pointInfo.x, pointInfo.y, pointInfo.size);
  },
  draw: function draw(cfg, container) {
    var attrs = _getAttrs(cfg);

    var path = _getPath(cfg.points);

    path = this.parsePath(path);
    return container.addShape('path', {
      attrs: _.mix(attrs, {
        path: path
      })
    });
  },
  getMarkerStyle: function getMarkerStyle(markerCfg) {
    return {
      symbol: function symbol(x, y, r) {
        var yValues = [y + 7.5, y + 3, y - 3, y - 7.5];

        var points = _getPoints(x, yValues, r);

        return [['M', points[0].x, points[0].y], ['L', points[1].x, points[1].y], ['M', points[2].x, points[2].y], ['L', points[3].x, points[3].y], ['L', points[4].x, points[4].y], ['L', points[5].x, points[5].y], ['Z'], ['M', points[6].x, points[6].y], ['L', points[7].x, points[7].y]];
      },
      lineWidth: 1,
      stroke: markerCfg.color,
      fill: markerCfg.color,
      radius: 6
    };
  }
});
export default KLineShapeFactory;